@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,400,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.books-grid {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap; }

.books-grid li {
  padding: 10px 15px;
  text-align: left; }

.book {
  width: 140px; }

.book-title,
.book-authors {
  font-size: 0.9em; }

.book-title {
  margin-top: 10px; }

.book-authors {
  color: #999; }

.bookmark {
  position: absolute;
  padding: 5px;
  color: #fff;
  background-color: #269f61;
  top: 0;
  font-size: 10px; }

.book-top {
  position: relative;
  height: 200px;
  display: flex;
  align-items: flex-end; }

.book-shelf-changer {
  position: absolute;
  right: 0;
  bottom: -10px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #269f61;
  background-image: url(/myreads-live/static/media/arrow-drop-down.78c6f53b.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 20px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23); }

.book-shelf-changer select {
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer; }

/* book cover */
.book-cover {
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  background: #eee; }

.book-cover-title {
  padding: 20px 10px 0;
  text-align: center;
  font-size: 0.8em; }

html, body, .root {
  font-family: "Source Sans Pro", sans-serif;
  height: 100%;
  font-size: 18px; }

body {
  line-height: 1.5; }

body .app {
  background: #eee; }

h2 {
  text-align: center;
  font-size: 47px;
  font-weight: 300;
  border-bottom: 1px solid #e8e8e8; }

.center {
  text-align: center; }

::-moz-selection {
  background: #269f61;
  color: #fff; }

::selection {
  background: #269f61;
  color: #fff; }

::-moz-selection {
  background: #269f61;
  color: #fff; }

body::-webkit-scrollbar {
  width: 0.8em;
  background-color: #444; }

body::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); }

body::-webkit-scrollbar-thumb {
  background-color: #269f61; }

.list-books-title {
  padding: 10px 0;
  background: #269f61;
  text-align: center; }

.list-books-title h1 {
  font-weight: 400;
  margin: 0;
  color: white; }

.list-books-content {
  padding: 0 0 80px;
  flex: 1 1; }

.bookshelf {
  padding: 0 10px 20px; }

.message-success {
  z-index: 1000;
  position: fixed;
  bottom: 15px;
  left: 5px;
  width: 350px;
  padding: 15px;
  background: rgba(41, 124, 50, 0.8);
  color: #fff;
  border-radius: 5px; }

.show {
  display: block; }

.hidden {
  display: none; }

.open-search {
  position: fixed;
  right: 25px;
  bottom: 25px; }

.open-search a {
  display: block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #269f61;
  background-image: url(/myreads-live/static/media/add.bb9363b8.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 28px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  font-size: 0; }

.search-books-bar {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 5;
  display: flex;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 0 6px rgba(0, 0, 0, 0.23); }

.search-books-bar input {
  width: 100%;
  padding: 15px 10px;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 1.25em;
  border: none;
  outline: none; }

.search-books-input-wrapper {
  flex: 1 1;
  background: #e9e; }

.close-search {
  display: block;
  top: 20px;
  left: 15px;
  width: 50px;
  height: 53px;
  background: white;
  background-image: url(/myreads-live/static/media/arrow-back.3687aa98.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 28px;
  font-size: 0; }

.search-books-results {
  padding: 80px 10px 20px; }

